  /* font-size: 9px; */
}
.ImageUser {
  margin-inline: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50px;
}
.ButtonList {
  border: none;
  cursor: pointer;
  background-color: #ae9b77;
  /* font-size: 16px; */
  font-weight: 600;
  width: 167px;
  height: 45px;
  color: #fff;
  text-align: "center";
  text-transform: "capitalize";
  border-radius: 10px;
  box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.2), 0 2px 7px 0 rgba(0, 0, 0, 0.1);
  border-color: rgb(148, 133, 133);
  border-color: rgb(138, 113, 93);
  border: solid;
  border-width: 2px;
  border-color: #ae9b77;
}

