@media only screen and (max-width: 1100px) {
  .estates {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 10px;
  }
}

@media only screen and (max-width: 900px) {
  /* For mobile phones: */
  .ButtonHeader {
    font-size: 10px;
  }
  .divUserCard {
    display: inline;
    width: 7%;
    font-size: 10px;
  }
  .ImageUser {
    margin-inline: 0px;
    width: 25px;
    height: 25px;
    border-radius: 50px;
  }
  .ButtonList {
    width: 100px;
    height: 36px;
    font-size: 10px;
    /* background-color: red; */
  }
  .Logo,
  .LogoFooter {
    width: 120px;
    height: 45px;
  }
  .textLatest {
    font-size: 15px;
  }
  .textabout p {
    font-size: 10px;
  }
  .textreal {
    font-size: 12px;
  }
  .continerAbout {
    /* background-color: red; */
    width: 320px;
    display: inline;
    align-items: center;
    text-align: center;
  }
  .textPrivacy {
    width: 400px;
    display: inline;
  }
}

@media only screen and (max-width: 750px) {
  /* For mobile phones: */
  .ButtonHeader {
    font-size: 8px;
    /* color: red; */
  }
  .divUserCard {
    display: inline;
    width: 7%;
    font-size: 8px;
    /* background-color: red; */
  }
  .ImageUser {
    margin-inline: 0px;
    width: 20px;
    height: 20px;
    border-radius: 50px;
  }
  .Logo,
  .LogoFooter {
    width: 50px;
    height: 20px;
  }
  .ButtonList {
    width: 70px;
    height: 30px;
    font-size: 7px;
  }
  .image {
    font-size: 20px;
  }
  .textLatest {
    font-size: 12px;
  }
  .textreal {
    font-size: 12px;
  }
  .footer p {
    font-size: 10px;
  }
  .textImage p {
    font-size: 10px;
    /* color: red; */
  }
}
@media only screen and (max-width: 500px) {
  /* For mobile phones: */
  .ButtonHeader {
    font-size: 7px;
  }
  .divUserCard {
    font-size: 7px;
  }
  .ButtonList {
    width: 60px;
    height: 25px;
    font-size: 6px;
    border-radius: 5px;
  }
  .image {
    font-size: 15px;
  }
  .footer p {
    font-size: 8px;
  }
  .continerAbout {
    width: 400px;
    display: inline;
  }
  .textPrivacy {
    width: 400px;
    display: inline;
  }
}
